<template>
  <div class="search">
    <form class="searchForm" @submit.prevent.stop="search">
      <div class="cover">
        <i></i>
        <input type="text" placeholder="搜索歌曲、歌手、专辑" @input="inputing" v-model="keywords"/>
      </div>
    </form>
    <component :is="showcomponent" @search="search" :keywords="keywords" class="whnb" @play="$emit('play',$event)"></component>
  </div>
</template>

<script>
// @ is an alias to /src
import SearchHotComponent from '@/components/SearchHotComponent'
import SearchSuggest from '@/components/SearchSuggest'
import SearchResult from '@/components/SearchResult'
export default {
  name: 'Search',
  data(){
    return {
      showcomponent:SearchHotComponent,
      keywords:''
    }
  },
  created(){
   
  },
  methods:{
    inputing(){
      //判断搜索输入的值不为空的时候显示搜索提示.
      if(this.keywords==''||this.keywords.length<=0)
      {
        this.showcomponent=SearchHotComponent;
      }else{
        this.showcomponent=SearchSuggest;
      }
    },
    search(event){
      if(event.keywords){//如果组件式传值需要取出关键词
        this.keywords=event.keywords;
      }
      this.showcomponent=SearchResult;
    }
  }
}
</script>
