.search{
  margin-top: 80px;
  // background: rgb(184, 227, 255);
  opacity:1;
  z-index: 1;
  background-size: 100% 200%;
  color: rgb(0, 0, 0);

  .searchForm{
    padding:15px 10px;
    z-index: 2;
    .cover{
          position: relative;
          width: 100%;
          height: 30px;
          padding: 0 15px;
          box-sizing: border-box;
          border: 2px solid #024983;
          background: #feffff;
          border-radius: 30px;
          display: flex;
          align-items: center;
          i{
            display: inline-block;
            background-image:url(data:image/svg+xml;base64,);
            width: 13px;
            height: 13px;
          }
          input{
            flex:1;
