<template>
    <div class="suggest">
        搜索提示:
        <ul>
            <li v-for="sl in suggestList" :key="sl.id" @click.stop="$emit('search',{keywords:sl.name})"><i></i><span>{{sl.name}}</span></li>
            <li v-show="suggestList.length<=0">暂无结果</li>
        </ul>
    </div>
</template>
<script>
export default {
    props:{
        keywords:String
    },
    data(){
        return {
            suggestList:[]
        }
    },
    created(){
       this.suggest(this.keywords);
    },
    watch:{
        keywords(newValue){
            this.suggest(newValue);
        }
    },
    methods:{
        suggest(keywords){
            //搜索结果包含 歌曲 专辑 歌手 歌单
            //order 搜结果字段
             this.axios.get('/search/suggest?keywords='+keywords).then(d=>{
                    console.log(d);
                if(d.data.result&&d.data.result.songs){
                    this.suggestList=d.data.result.songs;
                }
             });
        }
    }
}
</script>
<style lang="less" scoped>
.suggest{
    padding:0px 20px;
    font-size:16px;
        color: black;

    ul{
        li {
            line-height:45px;
            display: flex;
            align-items: center;
            font-size:15px;
            
            i{
                display: inline-block;
                width: 15px;
                height: 15px;
                margin-right:6px;
                background-image:url(data:image/svg+xml;base64,);
                background-repeat: no-repeat;
                
            }
        }
    }
}
</style>