<template>
    <div class="result">
        <h6>搜索结果:            <span class="nice1">{{keywords}}</span></h6>
        <ul class="kong">
            <img src="../assets/all.png" alt="">
            
            <li-item 
            v-for="(item,index) in searchresult" 
            :item="item" 
            :key="index"
            :keywords="keywords"
            @play="$emit('play',$event)"
            ></li-item>
            <Loading v-if="isLoading"></Loading>

        </ul>
        <!-- <p @click="loadmore">加载更多</p> -->
    </div>
</template>
<script>
import LiItem from '@/components/LiItem'
import Loading from '@/components/Loading'
export default {
    props:{
        keywords:String,

    },
    components:{
        LiItem,Loading
    },
    data(){
        return {
            searchresult:[],
            page:0,
            perpage:20,
            isLoading:true
        }
    },
    mounted(){
      window.addEventListener('scroll',this.debounce(this.handleScroll,1000))
    },
    beforeRountLeave(to,from,next){
      window.removeEventListener('scroll',this.debounce(this.handleScroll,1000));
      next();
    },
    methods:{
        debounce(fn, wait) {
          let timeout = null
          return function() {
            if(timeout !== null) clearTimeout(timeout)     
            timeout = setTimeout(fn, wait);
          }
        },
        handleScroll(){
        // console.log(document.documentElement.scrollTop);
        // console.log(document.documentElement.scrollHeight);
        // console.log(document.documentElement.clientHeight);
        let scrollA = document.documentElement.scrollHeight;
        let scrollC = document.documentElement.scrollTop;
        let scrollB = document.documentElement.clientHeight;
        let whnb = scrollA-(scrollC+scrollB);
        // console.log(whnb);
        if(whnb<3){
            // console.log('nb');
            // this.page++; // 1  2
            // let start=(this.page)*this.perpage;// 20   40
            // let end=(this.page+1)*this.perpage;// 40   60
            // let zzf = this.searchresult.slice(start,end)
            // this.hotList=this.hotList.concat(zzf);
            
            
            }
        
        },
        getresult(){
            this.axios.get('/search',{
                params:{
                    keywords:this.keywords,
                    limit:this.perpage,
                    offset:this.page*this.perpage,
                }
            }).then(d=>{
                // this.axios.get('/search?keywords='+this.keywords).then(d=>{
                // console.log(d);
                let result=d.data.result.songs.map((data)=>{
                    data.song={
                        artists:data.artists,
                        album:data.album
                    }
                    return data;
                    });
                    this.isLoading=false;
                    this.searchresult=this.searchresult.concat(result);
                })

            
        }
    },
    created(){
        // this.searchresult=[];
        this.getresult();
    }
}
</script>
<style lang="less" scoped>
.result{
    h6{
        font-size:14px;
        color:black;
        padding-left:10px;
    }
}
.nice1{
    color: rgb(38, 72, 226);
}
.kong{
  width: 100vw;
  position: relative;
  margin-bottom: 80px;
  img{
    position: absolute;
    opacity: 0.2;
    top: 0;
    left: 0;
    z-index: -3;

  }
}
</style>