<template>
    <div class="hotSearch">
        热门搜索:
        <ul>
            <li v-for="(item,index) in hotsearch" @click.stop="$emit('search',{keywords:item.first})" :key="index">{{item.first}}</li>
        </ul>
    </div>
</template>
<script>
export default {
    data(){
        return {
            hotsearch:[]
        }
    },
    created(){
        this.axios.get('/search/hot').then(d=>{
            this.hotsearch=d.data.result.hots;
        });
    }
}
</script>
<style lang="less" scoped>
.hotSearch{
    padding:0px 10px 10px 10px;
    font-size:16px;
    color: black;
    height: 60vh;
    background:url(../assets/all.png) no-repeat;
    background-size: 100% 150%;
    opacity: .9;
    // font-weight: bo;
    ul{
        overflow: hidden;
        padding-top:20px;
        li{
            border:1px solid rgb(38, 72, 226);;
            background-color: white;
            color:black;
            line-height:32px;
            padding:0px 14px;
            margin:0px 8px 8px 0px;
            border-radius: 32px;
            font-size:14px;
            float:left;
        }
    }
}
</style>